import React from 'react'
import './SearchPage.css'
import {useStateValue} from "../StateProvider"
import useGoogleSearch from "../useGoogleSearch"
import {Link} from 'react-router-dom'
import Search from '../components/Search'
import SearchIcon from '@mui/icons-material/Search'
import DescriptionIcon from '@mui/icons-material/Description'
import ImageIcon from "@mui/icons-material/Image"
import LocalOfferIcon from '@mui/icons-material/LocalOfferOutlined'
import RoomIcon from "@mui/icons-material/Room"
import MoreVertIcon from '@mui/icons-material/MoreVertOutlined'

async function SearchPage() {
  const [{term},dispatch]=useStateValue();
  const {data}=useGoogleSearch(term);
  console.log(data)
  return (
    <div className="searchPage">
      <div className="searchPage__header">
        <Link to="/">
          <img className='searchPage__logo' src="https://www.freepnglogos.com/uploads/google-logo-png/google-logo-png-google-icon-logo-png-transparent-svg-vector-bie-supply-14.png" alt="" />
        </Link>
        <div className="searchPage__headerBody">
        <Search buttons/>
        <div className="searchPage__options">
          <div className="searchPage__optionsLeft">
            <div className="searchPage__option">'
              <SearchIcon/>
              <Link to="/all">All</Link>
            </div>
            <div className="searchPage__option">
              <DescriptionIcon/>
              <Link to="/news">News</Link>
            </div>
            <div className="searchPage__option">
              <ImageIcon/>
              <Link to="/images">Images</Link>
            </div>
            <div className="searchPage__option">
              <LocalOfferIcon/>
              <Link to="/shopping">Shopping</Link>
            </div>
            <div className="searchPage__option">
              <RoomIcon/>
              <Link to="/maps">Maps</Link>
            </div>
            <div className='searchPage__option'>
              <MoreVertIcon/>
              <Link to="/more">More</Link>
            </div>
          </div>
          <div className="searchPage__optionsRight">
            <div className="searchPage__option">
              <Link to="/settings">Settings</Link>
            </div>
            <div className="searchPage__option">
              <Link to="/tools">Tools</Link>
            </div>
          </div>
        </div>
        </div>
      </div>
      {term && (

      <div className="searchPage__results">
        <p className="searchResults__count">
          About {data.searchInformation.formattedTotalResults} results found in 1 second for {term}
        </p>
      {data.map((res)=>{
        return (
        <div className="searchPage__Result">
          <a href={res.link}>{res.displayLink}</a>
          <a href={res.link} className="searchPage__resultTitle"><h2>{res.title}</h2></a>
          <p className="searchPage__resultSnippet">{res.snippet}</p>
        </div>

        )
      })}
      </div>
      )}
    </div>
  )
}

export default SearchPage